/* =============================================
  reset
============================================= */
/*
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com
Twitter: @rich_clark
*/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

/* =============================================
  base
============================================= */
html {
  font-size: 62.5%;
  width: calc(100vw - 15px);
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

a {
  text-decoration: none;
}

@media screen and (min-width: 769px) and (max-width: 1440px) {
  html {
    font-size: 0.625vw;
  }
}
@media screen and (min-width: 375px) and (max-width: 769px) {
  html {
    font-size: 0.625vw;
  }
}
/* =============================================
  header
============================================= */
.header {
  position: relative;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 4vw;
  height: 60px;
}
.header-logo {
  padding: 15px;
  max-width: 260px;
}
.header-hamburger {
  z-index: 100;
}

.nav {
  position: absolute;
  z-index: 80;
  top: 0;
  right: -42%;
  width: 40%;
  height: 100vh;
  padding-top: 20vh;
  background-color: #000;
  opacity: 0.8;
  transition: 1000ms;
}
.nav.active {
  right: 0;
}
.nav__item {
  font-size: 2rem;
  text-align: center;
}
.nav__link {
  display: block;
  padding: 20px 0;
  color: #fff;
}

.hamburger {
  z-index: 100;
  display: block;
  position: relative;
  cursor: pointer;
  width: 50px;
  height: 50px;
  background-color: transparent;
  border: none;
  outline: none;
  appearance: none;
  right: 40px;
}
.hamburger::after {
  position: absolute;
  content: "menu";
  color: #fff;
  right: -30px;
  top: 50%;
  transform: translateY(-50%);
}
.hamburger > span {
  display: inline-block;
  transition: all 0.4s;
  position: absolute;
  left: 14px;
  height: 3px;
  border-radius: 2px;
  background: #fff;
  width: 45%;
}
.hamburger > span:nth-of-type(1) {
  top: 15px;
}
.hamburger > span:nth-of-type(2) {
  top: 23px;
}
.hamburger > span:nth-of-type(3) {
  top: 31px;
}
.hamburger.active > span:nth-of-type(1) {
  top: 18px;
  left: 18px;
  transform: translateY(6px) rotate(-45deg);
  width: 30%;
}
.hamburger.active > span:nth-of-type(2) {
  opacity: 0;
}
.hamburger.active > span:nth-of-type(3) {
  top: 30px;
  left: 18px;
  transform: translateY(-6px) rotate(45deg);
  width: 30%;
}

/* =============================================
  footer
============================================= */
.footer {
  color: #fff;
  padding: 20px 0;
