  max-height: 607px;
  height: 100vh;
  margin-bottom: -160px !important;
}
.about-img {
  content: "";
  position: absolute;
  z-index: -100;
  height: inherit;
  max-height: inherit;
}
.about__box {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 3vw;
  background-color: #fff;
  max-width: 590px;
  max-height: 387px;
  padding: 50px 30px;
}

.works {
