}
.culture-img {
  padding-bottom: 30px;
}

.topics-box {
  max-width: 700px;
  margin-left: auto;
}
.topics-btn {
  margin-left: auto;
}

.contact-box {
  max-width: 440px;
}

.c-heading {
  color: #333;
  font-size: 3.2rem;
  padding-bottom: 30px;
}

.c-text {
  color: #333;
  font-size: 1.6rem;
  padding-bottom: 30px;
  line-height: 2;
}

.c-news-list {
  list-style: none;
}
.c-news-item {
  border-bottom: 1px solid #ddd;
  margin-bottom: 30px;
}
.c-news-link__news {
